<template>
    <div class="c_procedure">
        <CContentHeader content="流程管理" />
        <div class="c_tab">
            <!-- Tabs 标签页插件 -->
            <el-tabs v-model="activeName">
                <el-tab-pane label="流程中心" name="first">
                    <div class="c_procedure_main">
                        <router-link to="/procedureSetTemplate" class="c_tab_table_button">
                            <i class="el-icon-plus"></i> 新建流程
                        </router-link>

                        <!-- table表格插件 -->
                        <el-table :data="templateList" style="width: 100%">
                            <!-- <el-table-column min-width="230" prop="_name" label="流程名称"></el-table-column>
                            <el-table-column min-width="230" prop="_name" label="创建人"></el-table-column>
                            <el-table-column min-width="230" prop="_name" label="时间"></el-table-column>
                            <el-table-column min-width="230" prop="_name" label="流程状态"></el-table-column> -->
                            <el-table-column prop="name" label="流程名称" width="320"></el-table-column>
                            <el-table-column prop="create_user_name" label="创建人" width="310"></el-table-column>
                            <el-table-column prop="time" label="时间" width="320"></el-table-column>
                            <el-table-column prop="statuName" label="流程状态" min-width="240"></el-table-column>
                            <el-table-column width="340" label="操作">
                                <template slot-scope="scope">
                                    <!-- 开关 -->
                                    <div class="c_operate flex">
                                        <!-- <p>已启用</p> -->
                                        <!-- abdc_list_con_opearte_wrap_active abdc_list_con_opearte_wrap_nactive  -->
                                        <!-- <div class="c_operate_wrap c_operate_wrap_active">
                                        <img class="c_operate_icon" :src="require('../../assets/img/addressBook/slide.png')">
                                    </div> -->
                                        <p>{{(scope.row.status === 1)?'已启用':'已停止' }}</p>
                                        <!-- abdc_list_con_opearte_wrap_active abdc_list_con_opearte_wrap_nactive 
                                        :class="[(scope.row.status === 1)?'c_operate_wrap_nactive':'c_operate_wrap_nactive']"
                                     -->
                                        <div class="c_operate_wrap" :class="[(scope.row.status === 1)?'c_operate_wrap_nactive':'c_operate_wrap_active']" @click="handleEnable(scope.row)">
                                            <img class="c_operate_icon" :src="require('../../assets/img/addressBook/slide.png')" />
                                        </div>
                                    </div>
                                    <div class="c_procedure_right" @mouseleave="scope.row.isShowUpdate = false">
                                        <div @click="scope.row.isShowUpdate = true" class="c_procedure_icon">
                                        <!-- <div @click="ceshi" class="c_procedure_icon"> -->
                                            <i class="el-icon-more"></i>
                                        </div>
                                        <div v-if="templateList.length < 9" v-show="scope.row.isShowUpdate" class="c_procedure_operate">
                                            <div class="c_procedure_operate_item">
                                                <router-link :to="`/procedureEdit/${scope.row.name}/${scope.row._id}`" class="c_procedure_operate_item_a">修改</router-link>
                                            </div>
                                            <div @click="delBtn(scope.row,scope.$index)" class="c_procedure_operate_item">删除</div>
                                        </div>
                                        <div v-else v-show="scope.row.isShowUpdate" :class="[scope.$index < (templateList.length - 2) ? 'c_procedure_operate' : 'c_procedure_operate_top']">
                                            <div class="c_procedure_operate_item">
                                                <router-link :to="`/procedureEdit/${scope.row.name}/${scope.row._id}`" class="c_procedure_operate_item_a">修改</router-link>
                                            </div>
                                            <div @click="delBtn(scope.row,scope.$index)" class="c_procedure_operate_item">删除</div>
                                        </div>
                                    </div>
                                </template>
                            </el-table-column>
                        </el-table>

                        <!-- <div class="c_pagination">
                            <el-pagination @current-change="handleCurrentChange" :current-page.sync="pageIndex" :page-size="pageSize" layout="prev, pager, next, jumper" :total="pageTotal">
                            </el-pagination>
                        </div> -->
                    </div>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                activeName: 'first',
                //分页
                pageIndex: 1,
                pageSize: 10,
                pageTotal: 100,
                type: 1,
                // 表格数据
                templateList: [{
                    name: '曲靖APP新建申请',
                    province: '管理员',
                    city: '2019-06-05 16:40',
                    address: '应用中'
                }, {
                    name: '曲靖APP新建申请',
                    province: '管理员',
                    city: '2019-06-05 16:40',
                    address: '应用中'
                }, {
                    name: '曲靖APP新建申请',
                    province: '管理员',
                    city: '2019-06-05 16:40',
                    address: '应用中'
                }, {
                    name: '曲靖APP新建申请',
                    province: '管理员',
                    city: '2019-06-05 16:40',
                    address: '应用中'
                }, {
                    name: '曲靖APP新建申请',
                    province: '管理员',
                    city: '2019-06-05 16:40',
                    address: '应用中'
                }],
                userid: 'zxp17', //用户id
                company_id: '5d1463a61609d536e0bf2a1e' //用户所在的企业id
            };
        },
        mounted() {
            this.getTemplateList();
        },
        methods: {
            ceshi(event){
                console.log(event);
                // console.log(row);
                
            },
            async delBtn(row,index) {
                console.log(row);

                let res = await this.$zero.DeleteTips(`是否删除“${row.name}”流程`);
                if (!res) {
                    return;
                }
                console.log('删除了');
                let result = await this.$api.process.delTemplate({
                    m_id: row._id
                })
                console.log(result);
                if (result.statusCode === 75200) {
                    this.$zero.ToastSuccess("删除成功");
                    // this.templateList()
                    this.getTemplateList();
                }else{
                    return this.$zero.ToastError(result.msg);
                }

            },
            handleEnable(row) { //流程状态控制开关
                console.log(row);
                let changeType = row.status;

                changeType = changeType === 1 ? 2 : 1;
                // console.log(changeType);
                let obj = {
                    status: changeType,
                    m_id: row._id
                }
                this.statusTemplate(obj, row)
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            async statusTemplate(obj, row) { //流程状态接口
                let res = await this.$api.process.statusTemplate(obj)
                console.log(res);
                if (res.statusCode !== 75200) {
                    return this.$zero.ToastError(`修改状态失败：${res.msg}`);
                }
                this.$zero.ToastSuccess("状态修改成功");
                row.status = obj.status;
                if (obj.status === 1) {
                    row.statuName = '应用中';
                } else {
                    row.statuName = '已停用';
                }
            },
            async getTemplateList() { //获取流程列表
                // return;
                let res = await this.$api.process.getTemplateList({
                    company_id: this.company_id,
                    userid: this.userid,
                    type: this.type
                })

                console.log(res);
                if (res.statusCode !== 75200) {
                    return this.$zero.ToastWarning(res.msg)
                }
                this.templateList = JSON.parse(JSON.stringify(res.list))
                for (let i = 0; i < this.templateList.length; i++) {
                    // const element = res.list[i];
                    // const element = this.$zero.formatDate(res.list[i].meta.createAt)
                    // console.log(this.templateList[i].meta.createAt);
                    let str = this.templateList[i].meta.createAt;
                    let time = str.substring(0, str.indexOf('T')) + ' ' + str.substring(str.indexOf('T') + 1, str.indexOf('.')); //修改时间格式
                    // console.log(time);
                    this.templateList[i].time = time; //时间

                    this.$set(this.templateList[i], 'isShowUpdate', false);
                    // this.templateList[i].isShowUpdate = true;

                    if (this.templateList[i].status === 1) {
                        this.templateList[i].statuName = '应用中'
                    } else {
                        this.templateList[i].statuName = '已停用'
                    }
                }


                // for (let i = 0; i < this.templateList.length; i++) {
                //     if(this.templateList[i].status === 1){
                //         this.templateList[i].statuName = '应用中'
                //     }else{
                //         this.templateList[i].statuName = '已停用'
                //     }

                // }
                console.log(this.templateList);


            },
            showUpdate(row) {
                console.log(row);
                row.isShowUpdate = true;

            }
        }
    };
</script>


<style>


    .c_tab,
    .c_procedure {
        box-sizing: border-box;
        width: 100%;
    }

    /* tab栏样式 */
    .c_procedure .c_tab .el-tabs__nav-scroll {
        padding-left: 40px;
        background-color: #fff;
        padding-top: 5px;
    }

    .c_procedure .c_tab .el-tabs__nav-wrap::after {
        height: 0px;
    }

    .c_procedure .c_tab .el-tabs__active-bar {
        background-color: #5b94f2;
    }

    .c_procedure .c_tab .el-tabs__item.is-active {
        color: #141414;
    }

    .c_procedure .c_procedure_main {
        box-sizing: border-box;
        width: 100%;
        padding: 0 40px;
    }

    /* 新建流程按钮样式 */
    .c_procedure .c_tab_table_button {
        display: inline-block;
        text-decoration: none;
        border-radius: 0px;
        margin-top: 15px;
        margin-bottom: 20px;
        padding: 10px 40px 10px 20px;
        background-color: #2f78ef;
        border-color: #2f78ef;
        color: #fff;
        font-size: 14px;
    }

    /* 设置表格的右偏移 */
    .c_procedure .c_procedure_main .el-table td:first-child,
    .c_procedure .c_procedure_main .el-table th:first-child {
        padding-left: 40px;
    }

    .c_procedure .c_procedure_main .el-table th:nth-child(5) {
        padding-left: 45px;
    }


    .c_procedure .c_procedure_main .el-table th {
        padding: 8px 0;
        color: #999999;
    }

    .c_procedure .c_procedure_main .el-table td {
        border-bottom: 1px solid #ebebeb;
        color: #4d4d4d;
        /* padding: 0px; */
    }

    /* 表格头下划线 */
    .c_procedure .c_procedure_main .el-table th.is-leaf {
        border-bottom: 1px solid #d9d9d9;
    }

    .c_procedure .c_procedure_main .cell {
        display: flex;
        justify-content: space-between;
    }

    .c_procedure .c_procedure_main .c_procedure_icon {
        color: #d9d9d9;
        font-size: 16px;
        margin-right: 40px;
        cursor: pointer;
        position: relative;
    }

    .c_procedure .c_procedure_main .c_procedure_icon:hover {
        color: #2f78ef;
    }

    .c_procedure_operate {
        position: absolute;
        width: 80px;
        line-height: 35px;
        background-color: #fff;
        /* text-indent: 17px; */
        /* padding-top: 15px; */
        z-index: 999;
        box-shadow: 0 0 3px 0 #C8C8C8, 0 0 8px 0 rgba(0, 0, 0, .12);
        /* top: 35px; */
        right: 15px;
        top: 35px;
        text-align: center;
        z-index: 999;
    }

    .c_procedure_operate_top{
        position: absolute;
        width: 80px;
        line-height: 35px;
        background-color: #fff;
        /* text-indent: 17px; */
        /* padding-top: 15px; */
        z-index: 999;
        box-shadow: 0 0 3px 0 #C8C8C8, 0 0 8px 0 rgba(0, 0, 0, .12);
        text-align: center;
        right: 15px;
        bottom: 35px;
    }

    /* .c_procedure_main .el-table__row:last-child .c_procedure_operate{
        top: -10px;
    } */

    .c_procedure_operate .c_procedure_operate_item:hover,
    .c_procedure_operate_top .c_procedure_operate_item:hover {
        background-color: rgba(47, 120, 239, 0.1);
        /* background-clip: red; */
        cursor: pointer;
    }

    .c_procedure_operate_item_a {
        text-decoration: none;
        color: #4d4d4d;

    }


    /* 分页样式 */
    .c_procedure .c_pagination {
        width: 100%;
        height: 60px;
        border-top: 1px solid #d9d9d9;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
    }


    /* 开关样式 */
    .c_procedure .c_operate_icon {
        width: 15px;
        height: 15px;
        padding: 4px 0 0 5px;
    }

    .c_procedure .c_operate_wrap {
        position: relative;
        cursor: pointer;
        margin-left: 5px;
    }

    .c_procedure .c_operate>p {
        font-size: 14px;
        color: #999999;
    }

    .c_procedure .c_operate_wrap_active::after {
        content: "";
        position: absolute;
        top: 9px;
        right: -48px;
        width: 48px;
        height: 5px;
        border-radius: 5px;
        background: #d9d9d9;
    }

    .c_procedure .c_operate_wrap_nactive img {
        position: absolute;
        left: 48px;
    }

    .c_procedure .c_operate_wrap_nactive::before {
        content: "";
        position: absolute;
        top: 9px;
        left: 5px;
        width: 48px;
        height: 5px;
        border-radius: 5px;
        background: #2f78ef;
    }

    .c_procedure .el-table__body-wrapper {
        height: 480px;
        overflow-y: auto;
    }


    /* .el-table,
    .el-table__body-wrapper,
    .el-table__body,
    .el-table .cell, .el-table th div,
    .el-tabs__content{
        overflow: visible;
    } */
</style>